import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule } from '@angular/router'
import { FormsModule, ReactiveFormsModule } from "@angular/forms";

import { AppComponent } from './app.component';
import { ApplicantService } from './service/applicant.service';
import { ApplicantComponent } from './applicant/applicant.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { DataService } from './service/data.service';
import { RegisterService } from './service/register.service';
import { CustomEmailValidator } from './commons/validators/email.validator';
import { ValidationService } from './service/validation.service';
import { LoginService } from './service/login.service';
import { AuthGuardService } from './service/auth-guard.service';
import { NotAllowedComponent } from './not-allowed/not-allowed.component';
import { JobsComponent } from './jobs/jobs.component';
import { JobService } from './service/job.service';
import { AuthAccessService } from './service/AuthAccess.service';
import { BasicAuthInterceptor } from './service/BasicAuthInterceptor';
import { NotLoggedInComponent } from './not-logged-in/not-logged-in.component';

@NgModule({
  declarations: [
    AppComponent,
    ApplicantComponent,
    NavbarComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    NotAllowedComponent,
    JobsComponent,
    NotLoggedInComponent
  ],
  imports: [
    HttpClientModule,
    ReactiveFormsModule,
    FormsModule,
    BrowserModule,
    HttpModule,
    RouterModule.forRoot([
      {
        path: "register",
        component:  RegisterComponent
      }
      ,
      {
        path: "jobs",
        component: JobsComponent,
        canActivate: [AuthAccessService]
      },
      {
        path: 'notLoggedIn',
        component: NotLoggedInComponent
      },
      {
        path:'login',
        component: LoginComponent
      },
      {
        path:'',
        component: HomeComponent
      },
      {
        path:'applicant',
        component: ApplicantComponent,
        canActivate: [AuthAccessService, AuthGuardService]
      },
      {
        path: 'notallowed',
        component: NotAllowedComponent,
      }
    ])

  ],
  providers: [
              ApplicantService,
              JobService,
              LoginService,
              AuthGuardService,
              RegisterService,
              DataService,
              ValidationService,
              CustomEmailValidator,
              {provide: HTTP_INTERCEPTORS, useClass: BasicAuthInterceptor, multi: true}
            ],
  bootstrap: [AppComponent]
})
export class AppModule { }
